table{
    border-radius:4px;
    font-family: sans-serif, Calibri, Gill Sans, Gill Sans MT,  Trebuchet MS;
}
table, th, td {
    border: 1px solid grey;
    border-collapse: collapse;
    text-align:center;
    font-size: 12px;
}
table th{
  font-weight: 600;
  background-color: rgb(9, 9, 10);
  color: rgb(239, 238, 238);
  padding: 10px;
}
th ,td{
    width:fit-content;
    min-height:40px;
}

.input-field{
  width:95%;
  height:100%;
  border:none;
  text-align: center;
  outline:none;
}

.input-field:focus,

.input-field:active{
  border:1px solid rgb(91, 85, 85) !important;
}
